import { LayoutView } from "./layoutView";
import Prism from "prismjs";
import "prismjs/components/prism-css";
import "prismjs/components/prism-markup";
import "prismjs/plugins/line-numbers/prism-line-numbers.js";
import "prismjs/plugins/line-numbers/prism-line-numbers.css";
import { levelsList } from "../model/levels";
import { createElement } from "../model/element";
export class HtmlEditorView extends LayoutView {
    constructor() {
        super();
        this.area = null;
    }
    usePrismHighlighter(currentLevel) {
        const levelCode = levelsList[currentLevel].markup;
        const html = Prism.highlight(levelCode, Prism.languages.markup, "markup");
        return html;
    }
    drawEditor(currentLevel) {
        this.area = document.querySelector(".html-editor > .editor-area");
        if (this.area) {
            const pre = createElement(this.area, "pre", {
                className: "line-numbers",
            });
            const htmlCode = createElement(pre, "code", {
                className: "language-markup html-code",
                contentEditable: "true",
            });
            htmlCode.innerHTML = this.usePrismHighlighter(currentLevel - 1);
        }
    }
    updateView(currentLevel) {
        const code = document.querySelector(".html-code");
        if (code) {
            code.innerHTML = "";
            code.innerHTML = this.usePrismHighlighter(currentLevel - 1);
        }
    }
}
