import { createElement } from "../model/element";
import { LayoutView } from "./layoutView";
export class cssEditorView extends LayoutView {
    constructor() {
        super();
        this.area = null;
    }
    draw() {
        this.area = document.querySelector(".css-editor > .editor-area");
        if (this.area) {
            const pre = createElement(this.area, "pre", {
                className: "line-numbers",
            });
            const cssCode = createElement(pre, "code", {
                className: "language-css css-code",
                contentEditable: "true",
            });
        }
        const editorsRow = document.querySelector(".editors-row");
        const enterBtn = createElement(editorsRow, "button", {
            className: "btn btn-primary",
            textContent: "enter",
        });
    }
    updateView() {
        const cssCode = document.querySelector(".css-code");
        cssCode.innerHTML = "";
    }
}
