import { EventEmitter } from "../controller/eventEmitter";
import { LayoutView } from "./layoutView";
import { levelsList } from "../model/levels";
import { monsters } from "../model/monsters";
import { createElement } from "../model/element";
import { gameState } from "../model/state";
export class GameFieldView extends EventEmitter {
    constructor() {
        super();
        this.view = new LayoutView();
        this.gameScreen = null;
        this.main = null;
        this.currentLevel = gameState.currentLevel;
    }
    renderMonsters(monsters, levels, currentLevel) {
        const content = [];
        const monstersToShow = levelsList[currentLevel].monstersShown;
        const monstersToSelect = levelsList[currentLevel].monstersSelected;
        monsters.forEach((monster) => {
            let currentMonsterTooltip = "";
            let monsterSelectClass = "";
            if (monstersToShow.includes(monster.id)) {
                if (monstersToSelect.includes(monster.id)) {
                    monsterSelectClass = " monster-selected";
                }
                if (monster.tooltip) {
                    currentMonsterTooltip = monster.tooltip[currentLevel + 1];
                }
                content.push(`<div class="monster monster0${monster.id}${monsterSelectClass}"><div class="tooltip">${currentMonsterTooltip}</div></div>`);
            }
            else {
                content.push(`<div class="monster monster0${monster.id} monster-inv"></div>`);
            }
        });
        return content;
    }
    updateView(currentLevel) {
        this.gameScreen = document.querySelector(".game-screen");
        if (this.gameScreen) {
            this.gameScreen.innerHTML = "";
            const upperRow = createElement(this.gameScreen, "div", {
                className: "monsters-row upper-row",
            });
            const lowerRow = createElement(this.gameScreen, "div", {
                className: "monsters-row lower-row",
            });
            upperRow.innerHTML = "";
            lowerRow.innerHTML = "";
            const renderedMosters = this.renderMonsters(monsters, levelsList, currentLevel);
            renderedMosters.forEach((monster, i) => {
                i < renderedMosters.length / 2
                    ? (upperRow.innerHTML += monster)
                    : (lowerRow.innerHTML += monster);
            });
        }
        this.renterTooltips();
    }
    //tooltips
    renterTooltips() {
        const monsters = document.querySelectorAll(".monster-selected");
        monsters.forEach((monster) => {
            const tooltip = monster.querySelector(".tooltip");
            monster.addEventListener("mouseover", () => {
                if (tooltip) {
                    tooltip.style.opacity = "1";
                }
            });
            monster.addEventListener("mouseleave", () => {
                if (tooltip) {
                    tooltip.style.opacity = "0";
                }
            });
        });
    }
    animationOnWin() {
        const monsters = document.querySelectorAll(".monster-selected");
        monsters.forEach((monster) => {
            monster.classList.add("monster-deleted");
        });
    }
    draw(level) {
        const game = document.querySelector(".game");
        const gameScreen = document.createElement("div");
        gameScreen.classList.add("game-screen");
        game.appendChild(gameScreen);
        this.updateView(level);
    }
}
